#sendbtn,
#sendbtn:visited {
  background-color: #e0d2b4;
  color: #044444;
  border: #044444;
  font-family: "El Messiri";
  font-weight: bold;
  font-size: 1.2rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  float: right;
}
#sendbtn:hover {
  background-color: #ad976a;
  color: #044444;
  border: #044444;
}
#sendbtn:focus,
#sendbtn:focus-visible,
#sendbtn:checked {
  background-color: #e0d2b4;
  color: #044444;
  border: #044444;
}
