.emotionBox2{
  background: rgba(0, 0, 0, 0.3);
  width: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  color: #e0d2b4;
  border: 4px;
  border-style: solid;
  border-color: rgb(168, 223, 215);
  font-family: 'El Messiri';
  padding: 20px;
}

.form-control:not(focus){
  background: rgba(0, 0, 0, 0.3);
  color:#e0d2b4;
}

.form-control:focus{
  background: rgba(0, 0, 0, 0.3);
  color:#e0d2b4;
}
